<template>
	<div id="promote">
		<div class="header">
			<img class="lt-arrow" src="../../../assets/arrow.png" @click="goBack"/>
		</div>
		<div class="user-info">
			<div class="user-header">
				<img class="head-img" :src="myInfo.headImagePath"/>
				<!--<div class="user-name">用户名</div>-->
			</div>
			<div class="user-income">
				<div class="income-info">
					<em>￥</em>
					<div class="num">{{myInfo.incomeToday}}</div>
					<div class="income-text">今日收入</div>
					<div class="line"></div>
				</div>
				
				<div class="income-info">
					<em>￥</em>
					<div class="num">{{myInfo.incomeTotal}}</div>
					<div class="income-text">总收入</div>
				</div>
			</div>
		</div>
		<div class="btns">
			<div class="member btn" @click="linkTo('Promoters')">
				成为推广员
			</div>
			<div class="invite btn" @click="invite">
				立即邀请好友
			</div>
		</div>
		
		<div class="footer">
			<div class="title">
				推广员玩法说明：
			</div>
			<div class="content">
				1.成为推广员之后，通过你分享的注册的礼物飞会员购物你就可以赚到高额佣金。<br />
				2.佣金以零钱方式入账。好友每一次购物你都可以获得佣金。<br />
				3.当好友成为推广员之后，上级推广员可以获得下级推广员推广返利的间接返利。<br />
				4.在参与活动过程中，如发现违规行为（包括但不限于恶意批量注册、无效手机号码、虚假信息等）将封停账号，并撤销获得的相关优惠。
			</div>
		</div>
		
	</div>
</template>

<script>
	
export default {
	data(){
		return {
			userId:'',
			myInfo:{}
		}
	},
  components: {
  	
  },
  created(){
  	this.getMyMarketingInfo()
  },
  methods:{
  	invite(){
  		this.$popup({
  			content:'点击右上角邀请好友',
  			showCancel:false,
  		}).then((res)=>{
  			if(res == 'sure'){
  				
  			}
  		})
  	},
  	initShare(){
			let params = {
				title :"礼物飞好友邀请",
				desc  :"礼物飞好友邀请",
				link  :"https://api.who168.com/gift-fly/dist/#/promote/promotePhone?fromShare=1&shareBy="+this.userId,
				imgurl:"https://api.who168.com/lwflogo.png"
			}
			console.log(params);
			this.$wechat.iniShare(params)
		},
		initUser(){
			let user = JSON.parse( localStorage.getItem('UserDetails') )
			this.userId = user.id
			console.log(user)
			this.initShare()
		},
		getMyMarketingInfo(){
			this.$Api.getMyMarketingInfo(1).then((res)=>{
				console.log(res)
				if(res.q.s == 0){
					this.myInfo = res.q;
					this.initUser();
				}
			})
		}
  }
}
</script>

<style lang="scss">
	@import "../../../sass/baseSass.scss";
	#promote{
		min-height: 100%;
		background: url(../../../assets/promote/promote-bg.png) no-repeat;
		background-size: 100%;
		.header{
			position: fixed;
			padding: 0 0.4rem;
			height: 1.17rem;
			line-height: 1.17rem;
			.lt-arrow{
				width: 0.32rem;
				height: 0.59rem;
			}
		}
		.user-info{
			.user-header{
				padding-top: 5.07rem;
				text-align: center;
				/*width: 2.13rem;*/
				height: 2.13rem;
				border-radius: 100%;
				>img{
					height: 2.13rem;
					width: 2.13rem;
				}
				.user-name{
					font-size: 0.4rem;
					color: #fff;
				}

			}
			.user-income{
				display: flex;
				position: relative;
				padding-top: 3.2rem;
				justify-content: center;
				color: #fff;
				.income-info{
					position: relative;
					flex:1;
					text-align: center;
					>em{
						position: absolute;
						left: 1.33rem;
						top: 50%;
						transform: translateY(-50%);
						font-size: 0.4rem;
					}
					.num{
						font-size: 0.93rem;
					}
					.income-text{
						font-size: 0.32rem;
					}
				}
				.line{
					position: absolute;
					border-right: 1px solid #fff;
					height: 0.93rem;
					top: 50%;
					right: 0;
					transform: translateY(-50%);
				}
			}
		}
		.btns{
			padding-top: 2.13rem;
			.btn{
				width: 6.93rem;
				height: 1.07rem;
				font-size: 0.43rem;
				margin: 0 auto;
				line-height: 1.04rem;
				border-radius: 0.53rem;
				text-align: center;
			}
			.btn+.btn{
				margin-top: 0.27rem;
			}
			.member{
				border: 1px solid $label-color;
				background: #fff;
				color: $label-color;
			}
			.invite{
				@include bg-color;
				color: #fff; 
			}
		}
		.footer{
			padding-top: 0.67rem;
			.title{
				font-size: 0.4rem;
				padding: 0 0.4rem;
				color: #333;
			}
			.content{
				font-size: 0.35rem;
				margin-top: 0.35rem;
				padding: 0 0.4rem 0.4rem;
				text-align: left;
				color: #666;
			}
		}
	}
</style>


